@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;1,400;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
};

:root {
  --background:  #001528;
  --checker:  #7700ff;
  --white: #ffffff;
  --blue: #40578f;
  --font-color: #a9c6d9;
  --light-orange: #e55f3f;
  --light-yellow: #f1a845;
};
a {
    display: flex;
    justify-content: center;
    align-items: center;
    color: inherit;
    text-decoration: inherit;
}
